import React, {useState} from 'react'

function Contact() {

 const [data, setData] = useState({
     fullname:"",
     email:"",
     msg:""
 })

 const InputEvent = (event) =>{
     const {name, value} = event.target

     setData((preVal) =>{
         return{
             ...preVal,
             [name] : value,
         }
     })
 }

    const formSubmit = (e) =>{
        e.preventDefault()
        alert(
            `My name is ${data.fullname}, My Email is ${data.email} and Here i mentioned somthing that is ${data.msg}`
        )
    }
    return (
        <div>
           <div className="my-5">
               <h1 className="text-center">Contact Us</h1>
           </div>

           <div className="container contact_div">
               <div className="row">
                   <div className="col-md-6 col-10 mx-auto">
                       <form onSubmit={formSubmit}>

  <div class="form-group">
    <label for="exampleInputEmail1">Enter Your Full Name</label>
    <input type="text" name="fullname" value={data.fullname} onChange={InputEvent} placeholder="Name" required class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp"/>
  </div>
  <div class="form-group">
    <label for="exampleInputEmail1">Email address</label>
    <input type="email" name="email" value={data.email} onChange={InputEvent} placeholder="name@gmail.com" required class="form-control" id="exampleInputEmail1" aria-describedby="emailHelp"/>
    <small id="emailHelp"  class="form-text text-muted">We'll never share your email with anyone else.</small>
  </div>
  <div class="form-group">
    <label for="exampleInputPassword1">Password</label>
    <input type="password" class="form-control" id="exampleInputPassword1" required/>
  </div>

  <div class="form-group">
    <label for="exampleFormControlTextarea1">Comments</label>
    <textarea class="form-control" name="msg" value={data.msg} onChange={InputEvent} id="exampleFormControlTextarea1" rows="3"></textarea>
  </div>

  
  <button type="submit" class="btn btn-outline-primary">Submit</button>

                       </form>
                   </div>
               </div>
           </div>
        </div>
    )
}

export default Contact
